import React, {Component} from 'react';
import { getDefaultNormalizer } from '@testing-library/react';

class Aboutus extends Component{
    
    show = e => {
        let id = e.target.getAttribute("data-id");
        fetch(`http://localhost:3001/about_us/${id}`)
          .then(response => response.json())
         
          .then(data => {
            console.log(data);
            let content = 
            `<h1 data-id = ${id}>${data.name}</h1><hr/>
            <h3 data-id = ${id}>${data.description}</h3>`;
            document.getElementById(`img${id}`).innerHTML= content;
          });
      };

      search = e => {
        let inputt = document.getElementById("search").value
        console.log(inputt);
        for(var i=1;i<10;i++){
        fetch(`http://localhost:3001/about_us/${i}`)
          .then(response => response.json())
              
          .then(data => {
          
              if(data.name === inputt){
                let search = `<div className = "showup"><h2>We have the Service!</h2> <h3>Click <a href = "/services">here</a> to fill the  service</div>`;
                document.getElementById("content").innerHTML= search;
              } 
              
              
            }
            
          );}
      };
    render = () => {

        
        var style = {
            textAlign: 'center' ,
            color : 'rgb(26, 25, 25)',
            backgroundColor: 'rgba(255,255,255,0.5)',
            padding: '1%' 
          };
          var style2 = {
            textAlign: 'justify',
            margin: '0px 3% 0px 3%',
            letterSpacing: '0px'
          };
          var style3 = {
            fontStyle: 'italic'
          };
          var style4 = {
            marginTop: '-2.5%'
          };
          var style5 = {
            width: '80%',
            margin: '1% 5% 5% 10%'
          };
          var style6 = {
            textDecoration: 'none'
          };
        


        return(

            <div>

            <div id="breaker">
      
                <div className="aboutus" style={style}>
                    <h1>Welcome to House of Attorneys, A Law Firm in Kathmandu, Nepal.</h1> <br/><br/>
                    <h2 style={style2}>House of Attorneys is a full 
                    legal service firm 
                    practicing in the jurisdiction of Republic of Nepal.
                    Our attorneys are experienced in the areas of wide 
                    range of legal areas.</h2>
                    <br/><br/>
                    <h1>We Support you on the field of : </h1><br/> <input type = "text" id = "search" style = {{width : "50%"}} placeholder = "Search our service here..."/><input type="submit" onClick = {e =>{this.search(e)}} style={{height: '40px', width: '100px', border: '2px solid rgb(255, 255, 255)', backgroundColor: '#00203FFF', color: 'whitesmoke', cursor:'pointer'}}  />
                    <div id = "content"></div>


        <div className="imgrow">
        <div className="imgcolumn">


            <div className="overlaycontainer">
            <img src="images/About/civil-litigation.jpg" className="img1" alt = ""  onMouseOver= {e => {this.show(e);}} data-id = "1"   />
            <div className="overlay" id = 'img1' alt = ""></div> 
            </div>
            <h1 className="tbi">Civil Law</h1>
            
            <div className="overlaycontainer">
            <img src="images/About/tax.jpg" className="img2"  alt = ""  onMouseOver= {e => {this.show(e);}} data-id = "2" />
            <div className="overlay" id = 'img2' data-id = "2">
                </div> </div>
            <h1 className="tbi">Law of Taxation</h1>
            
        </div>



        <div className="imgcolumn">


            <div className="overlaycontainer"  >
            <img src="images/About/administrativelaw.jpg" className="img3" alt = "" data-id = "3" onMouseOver= {e => {this.show(e)}}/>
            <div className="overlay" id = 'img3' data-id = "3">
                </div></div>
            <h1 className="tbi">Administrative Law</h1>


            <div className="overlaycontainer" >
            <img src="images/About/business-and-corporate-law.jpg" className="img4" data-id = "4" alt = "" onMouseOver= {e => {this.show(e);}}/>
            <div className="overlay" id = 'img4' data-id = "4">
            </div></div>
            <h1 className="tbi">Business and Corpotate Law</h1>

        </div>



        <div className="imgcolumn" style={style4}>


            <div className="overlaycontainer" >
            <img src="images/About/foregininvestment.jpg" className="img5" data-id ="5" alt = "" onMouseOver= {e => {this.show(e);}}/>
            <div className="overlay"  id = 'img5' data-id = "5">
            </div></div>
            <h1 className="tbi">Foregin Investment Law</h1>


            <div className="overlaycontainer" >
            <img src="images/About/criminallaw.jpg" className="img6" data-id = "6" alt = "" onMouseOver= {e => {this.show(e);}}/>
            <div className="overlay" id = 'img6' data-id = "6">
            
            </div></div>
            <h1 className="tbi">Criminal Justice</h1>
        </div>

        <div className="imgcolumn">
            <div className="overlaycontainer" >
            <img src="images/About/bankinglaw.jpg" className="img7" data-id = "7" alt = "" onMouseOver= {e => {this.show(e);}}/>
            <div className="overlay" id = 'img7' data-id = "7">
               </div></div>
            <h1 className="tbi">Banking/Finance/Cooperative Law</h1>

            <div className="overlaycontainer" >
            <img src="images/About/translation.jpg"  className="img8" data-id = "8" alt = "" onMouseOver= {e => {this.show(e);}}/>
            <div className="overlay" id = 'img8' data-id = "8">
                </div></div>
            <h1 className="tbi">Legal Translation</h1>
        </div>
        </div>


        <div style={style5}>
        <h1 className="tbi">Miscellaneous</h1>
        <div className="overlaycontainer" >
        <img src="images/About/notary.jpeg" className="img9"  alt = ""/>
        <div className="overlaya" id = 'img9' data-id = "9">
        <h1>Miscellaneous</h1><hr/>
            <h3>Other serives like Notary, Employment Law, Constitutional law, Writ Jurisdiction, Legal Research, Hydro Power Law, Electricity Law and Information and Technology Law are also practiced in this firm.</h3>
           </div></div>
        
    </div>
    <a href="services.html" style={style6}><h2 className="assis">Get the assisitence of our attorneys RIGHT NOW! to be fully 
        and completely legal in every conscious step you take on your
    journey towards success or your journey of life. </h2></a>


                </div>
                </div>
                </div>
               
                
                    





            );
}
}

export default Aboutus;