import React, {Component} from 'react';
import { getDefaultNormalizer } from '@testing-library/react';
import axios from 'axios';

class Services extends Component{

  submit=(e)=>{
    e.preventDefault();
    const name = document.getElementById('fname')
    const dateOfBirth = document.getElementById('D.O.B')
    const phoneNo = document.getElementById('Phone No.')
    const Email = document.getElementById('email')
    const serviceType = document.getElementById('Service-Type')
    const state = document.getElementById('state')
    const District = document.getElementById('District')
    const Municipality = document.getElementById('Municipality')
    const Ward = document.getElementById('Ward')
    const Tstate = document.getElementById('Tstate')
    const TDistrict = document.getElementById('TDistrict')
    const TMunicipality = document.getElementById('Tmunicipality')
    const TWard = document.getElementById('TWard')
    const problem = document.getElementById('problem')
    const photodocument = document.getElementById('document')

        let data={  'Name': name.value,
                    'D.O.B': dateOfBirth.value,
                    'Phone No': phoneNo.value,
                    'Email': Email.value,
                    'Service Type': serviceType.value,
                    'State': state.value,
                    'District': District.value,
                    'Municipality': Municipality.value,
                    'Ward': Ward.value,
                    'Temporary State': Tstate.value,
                    'Temporary District': TDistrict.value,
                    'Temporary Municipality' : TMunicipality.value,
                    'Temporary Ward': TWard.value,
                    'Problem Description': problem.value,
                    'Document Image': photodocument.value,
        }
    axios.post('http://localhost:3001/clientRequests',data)
    .then(res=>{
        alert('Thanks for your submission '+name.value+'. Our attorneys will respond to you as soon as possible.')
    }
        )
        document.getElementById("servicesForm").reset();
}






    render = () => {

        var style = {
            backgroundColor : "rgba(255,255,255,0.85)",
            margin: '0',
            padding : '5%',
            textAlign: 'left'
          };
          var style2 = {
            margin: '10px 10px 10px 30px'
          };
          var style3 = {
            border: '2px solid rgb(255, 166, 0)',
            backgroundColor : "rgba(255,255,255,0.85)",
            padding: '2%'
          };
          var style4 = {
            margin: '15px 15px 10px 5px',
            textAlign: 'center'
          };
          var style5 = {
            margin: '15px 15px 10px 10px',
            textAlign: 'justify',
            fontWeight: 'bolder',
            wordSpacing: '5px',
            lineHeight: '30px'
          };
          


        return(
            
        <div className = "services">
            <div className = "rightservices" >
            <h2 style={style}>House Of Attorneys has provided legal services to different individuals, groups and bodies in different sectors.
            Here are some of the legal facilities that we provide.</h2>
            <ul style={style}>
                <li><h4 style={style2}>Criminal law</h4></li>
                <li><h4 style={style2}>Company and Corporate Law</h4></li>
                <li><h4 style={style2}>Banking Law</h4></li>
                <li><h4 style={style2}>Finance and Cooperative Law </h4></li>
                <li><h4 style={style2}>Administrative Law</h4></li>
                <li><h4 style={style2}>Employment Law</h4></li>
                <li><h4 style={style2}>Civil Law</h4></li>
                <li><h4 style={style2}>Constitutional law</h4></li>
                <li><h4 style={style2}>Writ Jurisdiction</h4></li>
                <li><h4 style={style2}>Legal Research</h4></li>
                <li><h4 style={style2}>Legal Translations</h4></li>
                <li><h4 style={style2}>Hydro Power Law</h4></li>
                <li><h4 style={style2}>Electricity Law</h4></li>
                <li><h4 style={style2}>Information and Technology Law</h4></li>
                <li><h4 style={style2}>Foreign Investment and Technology Transfer Law</h4></li>
                <li><h4 style={style2}>Tax Law</h4></li>
                </ul><br/>
                <hr/><br/>
                <div style={style3}>
                <h2 style={style4}>Legal Aid</h2>
                <div className = "cols">
                <p style={style5}>House 
                  of Attorneys provides legal aid or free legal service to needy and poor who have suffered injustice at the hands of
                   high handed government officials; who have been falsely accused of a crime which he/she never committed; who have 
                   been deprived of his/her share in the joint family for no justified reason; and
                  those who are suffering any other kind of legal injury for no justified reason.
                  To be eligible, the beneficiary must be poor and indigent to afford a lawyer; must come with clean
                   hands for legal aid; must not be the wrong doer and must have actually suffered injury.
                  </p></div></div><br/>
                  <h2 id="servi" style = {{marginTop : '-40px'}}>
                    Please <a href="contactus.html" style={{color: 'whitesmoke'}}>contact us</a> for details.</h2><br/>
                  <h2 style={{textAlign: 'center', backgroundColor : "rgba(255,255,255,0.85)", padding : "6%", marginTop : '-40px'}}>Please fill the form for further services of an attorney...</h2><br/>
        </div>



        <div className = "col-75">
          <div className = "container">
            <form id = "servicesForm">
            
                <div className = "col-50">
                 <br/> <h3 style= {{textAlign: 'center'}}>Your Details</h3>
                 <div className = "fitems">
                  <label for="fname"><i className = "fa fa-user"></i><h4> Full Name</h4></label>
                  <input type="text" id="fname" name="firstname" required placeholder="Your Name"/>
                  <label for="D.O.B"><i className = "fa fa-user"></i><h4>Date Of Birth</h4></label>
                  <input type="text" id="D.O.B" name="firstname" required placeholder="YYYY/MM/DD"/>
                  <label for="P.No"><i className = "fa fa-user"></i><h4>Phone No.</h4></label>
                  <input type="text" id="Phone No." name="firstname" required placeholder="Country Code and your number. Eg. +977-98********"/>
                  <label for="email"><i className = "fa fa-envelope"></i> <h4>Email</h4></label>
                  <input type="text" id="email" name="email" required placeholder="example@example.com"/>
                  <div className = "row">
                    <div className = "col-25">
                      <label for="Service-Type"><h4>Service-Type</h4></label>
                    </div>
                    <div className = "col-75">
                      <select id="Service-Type" name="Service-Type" style={{width : '100%', height: '40px'}}>
                        <option>...</option>
                        <option>Civil Law</option>
                        <option>Criminal law</option>
                        <option>Company and Corporate Law </option>
                        <option>Banking Law</option>
                        <option>Finance and Cooperative Law</option>
                        <option>Administrative Law</option>
                        <option>Employment Law</option>
                        <option>Constitutional law</option>
                        <option>Writ Jurisdiction</option>
                        <option>Legal Research</option>
                        <option>Legal Translations</option>
                        <option>Hydro Power Law</option>
                        <option>Electricity Law</option>
                        <option>Information and Technology Law</option>
                        <option>Foreign Investment and Technology Transfer Law</option>
                        <option>Tax Law</option>
                      </select>
                    </div>
                  </div>
                  <label for="adr"><i className = "address"></i><br/><h4>Permanent Address</h4></label>
                  <div className = "row">
                    <div className = "col-50">
                      <label for="state">State</label>
                      <input type="text" id="state" name="state" required placeholder="Your State"/>
                    </div>
                    <div className = "col-50">
                      <label for="District">District</label>
                      <input type="text" id="District" name="District" required placeholder="District"/>
                    </div>
                    <div className = "col-50">
                      <label for="District">Municipality</label>
                      <input type="text" id="Municipality" name="District" required placeholder="Municipality"/>
                    </div>
                    <div className = "col-50">
                      <label for="District">Ward No.</label>
                      <input type="text" id="Ward" name="District" required placeholder="Ward No."/>
                    </div>
                  </div>
                  
                    <label for="adr"><i className = "address"></i><br/><h4>Temporary Address</h4></label>
                    <div className = "row">
                      <div className = "col-50">
                        <label for="state">State</label>
                        <input type="text" id="Tstate" name="state" required placeholder="Your State"/>
                      </div>
                      <div className = "col-50">
                        <label for="District">District</label>
                        <input type="text" id="TDistrict" name="District" required placeholder="District"/>
                      </div>
                      <div className = "col-50">
                        <label for="District">Municipality</label>
                        <input type="text" id="Tmunicipality" name="District" required placeholder="Municipality"/>
                      </div>
                      <div className = "col-50">
                        <label for="District">Ward No.</label>
                        <input type="text" id="TWard" name="District" required placeholder="Ward No."/>
                      </div>
                    <div className = "row">
                        <div>
                          <label for="subject"><h4>Please write your problem in brief</h4></label>
                          <textarea name="" id="problem" cols="70" rows="10" placeholder="Write feedback here..." style={{width: '100%', border:  '1px solid orangered'}}></textarea><br/>
                        </div><br/>
                        Click here to upload an image
                      
                          <input type="file" name="pic" id = "document" accept="image/*"/><br/><br/>
                          
                          <input type="submit" onClick = {e => {this.submit(e)}} style={{height: '40px', width: '100px', border: '2px solid rgb(255, 255, 255)', backgroundColor: '#00203FFF', color: 'whitesmoke', cursor:'pointer', float: 'right'}}  />
                        
                      </div>
                </div>
              </div>
              </div>
            </form>
          </div>
        </div>

    </div>

            
            );
    }
}

export default Services;