import React, {Component} from 'react';
import { getDefaultNormalizer } from '@testing-library/react';


class Footer extends Component{
    render = () => {
      var style = {
        display: 'inline-block', 
        color: '#F95700FF' 
      };
      var style2 = {
        display: 'inline-block', 
        color: 'whitesmoke' 
      };
     
      var style4 = {
        textAlign: 'center'
      };
      var style5 = {
        lineHeight: '160%',
        textAlign: 'justify'
      };
      var style6 = {
        color : 'white',
        textAlign : 'center',
      };
      
        return (
          <div className="footer"> 
          <img src = "images/logo.png" />
          <div className = "footerheadingtext">
          <h1 style = {style} >House of Attorneys:</h1>
                <h1 style={style2} >:A Law Firm</h1>
                </div>
                
                <br/>
                <div className = "footerrighttext">
            <h1 style={style4}>  Legal Disclaimer</h1><br/>
            <h4 style={style5}> This is not an advertisement. visiting the website 
              alone does not establish an attorney-client relationship.
               Nothing contained in this website shall deemed to constitute 
               violation of Lawyers Professional Code of Ethics.</h4></div> <br/> <br/><br/> <br/>
    
      <h2 style = {style6}>A 2020 House of Attorneys' Copyright </h2>
      
      </div>  
            
        );
    }
}

export default Footer;