import React, {Component} from 'react';
import { getDefaultNormalizer } from '@testing-library/react';
import { Link } from 'react-router-dom';
import $ from 'jquery';
import {findDOMNode} from 'react-dom';

class Header extends Component{

afternav = () => {
    if ($('#afternav').css('opacity') === '0') {
        $('#afternav').css('opacity','1');
      }   
    else{
        $("#afternav").css("opacity","0");
     }
    }



    render = () => {
        var style = {
            display: 'inline-block', 
            color: '#F95700FF' 
          };
          var style2 = {
            display: 'inline-block', 
            color: 'whitesmoke' 
          };
         
        return (
            <div className="sticky"> 
            <div className="header">
            <Link to = '/'>
                <div className="heading">
                <h1 style = {style} >House of Attorneys:</h1>
                <h1 style={style2} >:A Law Firm</h1>
                </div></Link>
            
                <nav className="beforenav">
                    <ul>
                        <Link to = '/contactUs'><li id="contact">Contact Us</li></Link>
                        <Link to = '/profiles'><li>Profiles</li></Link>
                        <Link to = '/services'><li>Services</li></Link>
                        <Link to = '/aboutus'><li>About Us</li></Link>
                        <Link to = '/'><li>Home</li></Link>
                    </ul>
                </nav>
               
                <div className="nav">
                <h1 onClick ={this.afternav}>☰</h1>
            </div>
             
            
             <nav id="afternav">
                <ul>
                        <Link to = '/'><li>Home</li></Link>
                        <Link to = '/aboutus'><li>About Us</li></Link>
                        <Link to = '/services'><li>Services</li></Link>
                        <Link to = '/profiles'><li>Profiles</li></Link>
                <Link to = '/contactUs'><li id="contact">Contact Us</li></Link>
                </ul>
            </nav>
          </div>
        </div> 
        );
    }
}

export default Header;